// Uniswap Protocol

export const getCost = async (amount, counterTokenAddr) => {
  const exchange = new web3.eth.Contract(uniswapRouterAbi, UniswapV2Router02);
  // eslint-disable-next-line no-unused-vars
  const [inputAmount, _] = await exchange.methods.getAmountsIn(
    new BigNumber(amount).toFixed(),
    [counterTokenAddr, VTD.addr],
  ).call();
  return inputAmount;
};

export const getProceeds = async (amount, counterTokenAddr) => {
  const exchange = new web3.eth.Contract(uniswapRouterAbi, UniswapV2Router02);
  // eslint-disable-next-line no-unused-vars
  const [_, outputAmount] = await exchange.methods.getAmountsOut(
    new BigNumber(amount).toFixed(),
    [VTD.addr, counterTokenAddr],
  ).call();
  return outputAmount;
};

