export const getInstantaneousPrice = async (counterTokenAddr, pairAddress) => {
  const [reserve, token0] = await Promise.all([getReserves(pairAddress), getToken0(pairAddress)]);
  const token0Balance = new BigNumber(reserve._reserve0);
  const token1Balance = new BigNumber(reserve._reserve1);
  if (token0.toLowerCase() === counterTokenAddr.toLowerCase()) {
    // TODO set to DSD amounts
    return token0Balance.dividedBy(token1Balance);
    // return token0Balance.multipliedBy(new BigNumber(10).pow(12)).dividedBy(token1Balance);

  }
  return token1Balance.dividedBy(token0Balance);
  // return token1Balance.multipliedBy(new BigNumber(10).pow(12)).dividedBy(token0Balance);

};

