import React from 'react';
import BigNumber from 'bignumber.js';

import { StatsRowItem } from '../common/index';
import TextBlock from "../common/TextBlock";
import {ownership} from "../../utils/number";
import {formatBN} from "../../utils/number";

type AccountPageHeaderProps = {
  accountVTDBalance: BigNumber,
  accountVTDSBalance: BigNumber,
  totalVTDSSupply: BigNumber,
  accountStagedBalance: BigNumber,
  accountBondedBalance: BigNumber,
  accountStatus: number,
  unlocked: number,
};

function fm(bn){
  let num = (formatBN(bn, 2))
  return num;
}

const STATUS_MAP = ["Unlocked", "Locked", "Locked"];

function status(accountStatus, unlocked) {
  return STATUS_MAP[accountStatus] + (accountStatus === 0 ? "" : " until epoch:" + unlocked)
}

const AccountPageHeader = ({
  accountVTDBalance, accountVTDSBalance, totalVTDSSupply, accountStagedBalance, accountBondedBalance, accountStatus, unlocked
}: AccountPageHeaderProps) => (

<div style={{display: 'flex'}}>
     
  <div style={{ flex: '1 0 50%',  padding: '2%', display: 'flex', flexWrap: 'wrap', alignItems: 'center', justifyContent: 'space-between' }}>
      <div style={{ flex: '1 0 50%'}}>
      <StatsRowItem asset="Balance" value={fm(accountVTDBalance)} suffix={" VTD"}/>
      <StatsRowItem asset="Staged" value={fm(accountStagedBalance)}  suffix={" VTD"}/>
      <StatsRowItem asset="Bonded" value={fm(accountBondedBalance)}  suffix={" VTD"}/>
      </div>
  </div>
  <div style={{ flex: '1 0 50%', padding: '2%', display: 'flex', flexWrap: 'wrap', alignItems: 'center', justifyContent: 'space-between' }}>
      <div style={{flex: '1 0 50%'}}>
          <StatsRowItem asset="Dao Ownership" value={fm(ownership(accountVTDSBalance, totalVTDSSupply))} suffix={"%"} />
          <StatsRowItem asset="Dao status" value={status(accountStatus, unlocked)}  suffix={""}/>
      </div>
  </div>
</div>
);


export default AccountPageHeader;
