import React, { useState } from 'react';
import {
  Box, Button, IconArrowUp, IconCirclePlus
} from '@aragon/ui';
import BigNumber from 'bignumber.js';
import {
  BalanceBlock, MaxButton, PriceSection,
} from '../common/index';
import {approve, providePool} from '../../utils/web3';
import {isPos, toBaseUnitBN, toTokenUnitsBN} from '../../utils/number';
import {VTD} from "../../constants/tokens";
import {MAX_UINT256} from "../../constants/values";
import BigNumberInput from "../common/BigNumberInput";

type ProvideProps = {
  poolAddress: string,
  user: string,
  rewarded: BigNumber,
  pairBalanceVTD: BigNumber,
  pairBalancePeg: BigNumber,
  userPEGBalance: BigNumber,
  userPEGAllowance: BigNumber,
  status: number,
  peg: any,
};

function Provide({
  poolAddress, user, rewarded, pairBalanceVTD, pairBalancePeg, userPEGBalance, userPEGAllowance, status, peg
}: ProvideProps) {
  const [provideAmount, setProvideAmount] = useState(new BigNumber(0));
  const [usdcAmount, setUsdcAmount] = useState(new BigNumber(0));

  const PEGToVTDRatio = pairBalancePeg.isZero() ? new BigNumber(1) : pairBalancePeg.div(pairBalanceVTD);

  const onChangeAmountVTD = (amountVTD) => {
    if (!amountVTD) {
      setProvideAmount(new BigNumber(0));
      setUsdcAmount(new BigNumber(0));
      return;
    }

    const amountVTDBN = new BigNumber(amountVTD)
    setProvideAmount(amountVTDBN);

    const amountVTDBU = toBaseUnitBN(amountVTDBN, VTD.decimals);
    const newAmountPEG = toTokenUnitsBN(
      amountVTDBU.multipliedBy(PEGToVTDRatio).integerValue(BigNumber.ROUND_FLOOR),
      VTD.decimals);
    setUsdcAmount(newAmountPEG);
  };

  return (
    <Box heading="Provide">
      {userPEGAllowance.comparedTo(MAX_UINT256.dividedBy(2)) > 0 ?
        <div style={{display: 'flex', flexWrap: 'wrap'}}>
          {/* total rewarded */}
          <div style={{flexBasis: '32%'}}>
            <BalanceBlock asset="Rewarded" balance={rewarded} suffix={"VTD"} />
          </div>
          <div style={{flexBasis: '33%'}}>
            <BalanceBlock asset={`${peg.symbol} Balance`} balance={userPEGBalance} suffix={peg.symbol} />
          </div>
          <div style={{flexBasis: '2%'}}/>
          {/* Provide liquidity using Pool rewards */}
          <div style={{flexBasis: '33%', paddingTop: '2%'}}>
            <div style={{display: 'flex'}}>
              <div style={{width: '60%', minWidth: '6em'}}>
                <>
                  <BigNumberInput
                    adornment="VTD"
                    value={provideAmount}
                    setter={onChangeAmountVTD}
                    disabled={status === 1}
                  />
                  <PriceSection label="Requires " amt={usdcAmount} symbol={peg.symbol}/>
                  <MaxButton
                    onClick={() => {
                      onChangeAmountVTD(rewarded);
                    }}
                  />
                </>
              </div>
              <div style={{width: '40%', minWidth: '6em'}}>
                <Button
                  wide
                  icon={<IconArrowUp/>}
                  label="Provide"
                  onClick={() => {
                    providePool(
                      poolAddress,
                      toBaseUnitBN(provideAmount, VTD.decimals),
                      (hash) => setProvideAmount(new BigNumber(0))
                    );
                  }}
                  disabled={poolAddress === '' || status !== 0 || !isPos(provideAmount) || usdcAmount.isGreaterThan(userPEGBalance)}
                />
              </div>
            </div>
          </div>
        </div>
        :
        <div style={{display: 'flex', flexWrap: 'wrap'}}>
          {/* total rewarded */}
          <div style={{flexBasis: '32%'}}>
            <BalanceBlock asset="Rewarded" balance={rewarded} suffix={"VTD"} />
          </div>
          <div style={{flexBasis: '33%'}}>
            <BalanceBlock asset={`${peg.symbol} Balance`} balance={userPEGBalance} suffix={peg.symbol}/>
          </div>
          <div style={{flexBasis: '2%'}}/>
          {/* Approve Pool to spend DSD */}
          <div style={{flexBasis: '33%', paddingTop: '2%'}}>
            <Button
              wide
              icon={<IconCirclePlus/>}
              label="Approve"
              onClick={() => {
                approve(peg.addr, poolAddress);
              }}
              disabled={poolAddress === '' || user === ''}
            />
          </div>
        </div>
      }
      <div style={{width: '100%', paddingTop: '2%', textAlign: 'center'}}>
        <span style={{ opacity: 0.5 }}> Zap your rewards directly to LP by providing more ${peg.symbol} </span>
      </div>
    </Box>
  );
}

export default Provide;
