import React, { useState, useEffect } from 'react';
import { useParams } from 'react-router-dom';
import {Grid} from '@geist-ui/react'
import BigNumber from 'bignumber.js';
import {
  getBalanceBonded,
  getBalanceOfStaged, getFluidUntil, getLockedUntil,
  getStatusOf, getTokenAllowance,
  getTokenBalance,
  getCouponPremium,
  getPoolTotalClaimable, getPoolTotalRewarded,
  getTokenTotalSupply, getTotalBonded, getTotalCoupons, getTotalDebt, getTotalRedeemable, getTotalStaged,
  getEpoch, getEpochTime, getNextEpochTimestamp, getCurrentEpochLength,
} from '../../utils/infura';

import {formatBN, ownership} from "../../utils/number";

import {VTD, VTDD} from "../../constants/tokens";

import {BalanceBlock, StatsRowItem} from "../common/index";

import {DAO_EXIT_LOCKUP_EPOCHS, GOVERNANCE_PERIOD, POOL_EXIT_LOCKUP_EPOCHS} from "../../constants/values";
import { toTokenUnitsBN } from '../../utils/number';

import { MainContext, ContextType, IPeggedToken} from "../../store/MainStore";

const ONE_COUPON = new BigNumber(10).pow(18);

function Stats({ user }: {user: string}) {
  const { override } = useParams();
  if (override) {
    user = override;
  }

  const [couponPremium, setCouponPremium] = useState(new BigNumber(0));
  const [epoch, setEpoch] = useState(0);
  const [epochTime, setEpochTime] = useState(0);
  const [nextEpochTimestamp, setNextEpochTimestamp] = useState(0);
  const [currentEpochLength, setCurrentEpochLength] = useState(0);
  const [_pegs, setPegs] = useState([]);
  const { peggedTokens, totals, updatePools, updateTotals} = React.useContext(MainContext) as ContextType;
  
  let price =  new BigNumber(0);
  let lastTwap = new BigNumber(0);
  peggedTokens.filter((_peg) => {
    if (_peg.symbol === 'ETH') {
      price = _peg.stats.price.multipliedBy(_peg.usdcPrice)
      lastTwap = _peg.stats.lastTwap.multipliedBy(_peg.usdcPrice)
    }
  })
  
  const marketcap = totals.totalSupply.multipliedBy(price);
  const currentEpochLengthFmt = secondsToM(currentEpochLength);
  const infinity = '\u221E'; 

  function secondsToM(d) {
    d = Number(d);
    var m = Math.floor(d / 60);
    return m;
  }

  function fm(bn){
    let num = (formatBN(bn, 2));
    return num;
  }

  function inUsdc(amount, rate) {
    return amount.multipliedBy(rate)
  }

  function totalYield() {
      var delta = (price.toNumber()-1)/12
      delta = delta > 0.1 ? 0.1 : delta
      delta = epoch > 36 ? delta : 0.06
      return (totals.totalSupply.toNumber())*delta
  }

  function daoYield(){
    return (totalYield()*0.6/totals.totalBonded.toNumber()*100).toFixed(3)
  }

  function compound(rate, days) {
      let epochs_per_day = 24*60/currentEpochLengthFmt
      let times = days*epochs_per_day
      let real_rate =((1+rate/100)**times -1)*100 
      return real_rate < 5000000 ? real_rate.toFixed(3) : infinity
  }

  function LPYield(share, peg){
    const supply = peg.stats.vtdBalance;
    const vtdPrice = peg.stats.price.multipliedBy(peg.usdcPrice)
    const pegValue = peg.stats.pegBalance.multipliedBy(peg.usdcPrice)
    let lp_size = supply.multipliedBy(vtdPrice).multipliedBy(2)
    return (totalYield()*price.toNumber()*0.4/lp_size.toNumber()*share.toNumber()).toFixed(3)
  }

  function calcLinearYield(rate, days) {
    let epochs_per_day = 24*60/currentEpochLengthFmt
    let times = days*epochs_per_day
    return (rate*times).toFixed(3)
  }
  //Update User balances
  useEffect(() => {
    let isCancelled = false;

    async function updateUserInfo() {
        const [epochStr, epochTimeStr, nextEpochTimestampStr, currentEpochLengthStr] = await Promise.all([
            getEpoch(VTDD.addr),
            getEpochTime(VTDD.addr),
            getNextEpochTimestamp(VTDD.addr),
            getCurrentEpochLength(VTDD.addr),
        ]);

        if (!isCancelled) {
            setEpoch(parseInt(epochStr, 10));
            setEpochTime(parseInt(epochTimeStr, 10));
            setNextEpochTimestamp(parseInt(nextEpochTimestampStr, 10));
            setCurrentEpochLength(parseInt(currentEpochLengthStr, 10));

            if (totals.totalDebt.isGreaterThan(ONE_COUPON)) {
            const couponPremiumStr = await getCouponPremium(VTDD.addr, ONE_COUPON)
            setCouponPremium(toTokenUnitsBN(couponPremiumStr, VTD.decimals));
            } else {
            setCouponPremium(new BigNumber(0));
            }
            // setLockup(poolAddress === DollarPool4 ? DAO_EXIT_LOCKUP_EPOCHS : 1);
            
        }
    }
    updateUserInfo(); 
    updatePools(); 
    updateTotals();
  }, [user]);


  return (
    <>
    <Grid.Container gap={5} justify="center">
        <Grid xs={12} sm={6}>
        <BalanceBlock asset="Epoch Length" balance={currentEpochLengthFmt} suffix={" min"}/>

        </Grid>
        <Grid xs={12} sm={6}>
        <BalanceBlock asset="Epoch #" balance={epoch}  suffix={""}/>

        </Grid>
        <Grid xs={12} sm={6}>
        <BalanceBlock asset="Spot Price (USD)" balance={price} suffix={""} />
        </Grid>
        <Grid xs={12} sm={6}>
        <BalanceBlock asset="Last TWAP Price (USD)" balance={lastTwap} suffix={""} />
        </Grid>

    <Grid xs={24} sm={12}>

      <StatsRowItem asset="Market Cap ($)" value={fm(marketcap)} suffix={" USD"}/>
      <StatsRowItem asset="Tokens" value={fm(totals.totalSupply)}  suffix={""}/>
    </Grid>
    <Grid xs={24} sm={12}>
      <StatsRowItem asset="Coupon" value={fm(totals.totalCoupon)} suffix={""} />
      <StatsRowItem asset="Coupon Premium" value={fm(couponPremium)}  suffix={"%"}/>
    </Grid>
    <Grid xs={24} sm={12}>
      <div style={{ fontSize: 14, lineHeight: 2}}>Stats</div>
        <StatsRowItem asset="Circulating Supply" value={fm(totals.circulating)}  suffix={""}/>
        <StatsRowItem asset="DAO Staged" value={fm(totals.totalStaged)} suffix={""} />
        <StatsRowItem asset="DAO Bonded" value={fm(totals.totalBonded)}  suffix={""}/>
    </Grid>
    <Grid xs={24} sm={12}>
          <div style={{ fontSize: 14, lineHeight: 2}}>DAO Yield</div>

      <StatsRowItem asset="Day Yield" value={compound(daoYield(), 1)} suffix={"%"}/>
      <StatsRowItem asset="Month Yield" value={compound(daoYield(),30)}  suffix={"%"}/>
      <StatsRowItem asset="Annual Yield" value={compound(daoYield(), 365)} suffix={"%"} />
    </Grid>

     {
    //  @ts-ignore
    peggedTokens.map((peggedToken) =>
    //@ts-ignore
      peggedToken.hasOwnProperty('stats') && peggedToken.enabled ?
      <Grid xs={24} sm={12}>
        <div style={{ fontSize: 14, lineHeight: 2}}>{peggedToken.symbol} Pool: <small>{`1 ${peggedToken.symbol} = ${peggedToken.usdcPrice.toFixed(2)} USD`}</small></div>
        <StatsRowItem asset="Price" value={fm(peggedToken.stats.pegBalance.dividedBy(peggedToken.stats.vtdBalance))} suffix={peggedToken.symbol}/>
        <StatsRowItem asset="VTD Bonded" value={fm(peggedToken.stats.vtdBalance)} suffix={"VTD"}/>
        <StatsRowItem asset={`${peggedToken.symbol} Bonded`} value={fm(peggedToken.stats.pegBalance)} suffix={peggedToken.symbol}/>
        <div style={{marginTop: '10px', marginLeft: '20px'}}>
          <StatsRowItem asset="LP Rewarded" value={fm(peggedToken.stats.poolReward)} suffix={""} />
          <StatsRowItem asset="LP Claimable" value={fm(peggedToken.stats.poolClaimable)} suffix={""}/>
          <StatsRowItem asset="% LP Rewards" value={fm(ownership(peggedToken.stats.vtdBalance, totals.totalLPBonded))} suffix={"%"}/>

          <div style={{ fontSize: 14, lineHeight: 2}}>LP Yield (in USD)</div>

          <StatsRowItem asset="Day Yield" value={calcLinearYield(LPYield(ownership(peggedToken.stats.vtdBalance,totals.totalLPBonded), peggedToken),1)} suffix={"%"}/>
          <StatsRowItem asset="Month Yield" value={calcLinearYield(LPYield(ownership(peggedToken.stats.vtdBalance,totals.totalLPBonded), peggedToken),30)}  suffix={"%"}/>
          <StatsRowItem asset="Annual Yield" value={calcLinearYield(LPYield(ownership(peggedToken.stats.vtdBalance,totals.totalLPBonded), peggedToken), 365)} suffix={"%"} />
        </div>
        </Grid> : <></>
     )}
          <Grid xs={24} sm={12}>

                <div style={{ fontSize: 14, lineHeight: 2}}>Info:</div>

              <StatsRowItem asset="DAO Lockup" value={DAO_EXIT_LOCKUP_EPOCHS} suffix={""}/>
              <StatsRowItem asset="LP Lockup" value={POOL_EXIT_LOCKUP_EPOCHS}  suffix={""}/>
              <StatsRowItem asset="Governance Period" value={GOVERNANCE_PERIOD} suffix={""} />
          </Grid>
          <Grid xs={24} sm={12}>

                <div style={{ fontSize: 14, lineHeight: 2}}>Current Settings:</div>

                <StatsRowItem asset="Max Expansion" value={10} suffix={"%"}/>
                <StatsRowItem asset="Advance Reward" value='Reverse Dutch Auction'  suffix={""}/>
                <StatsRowItem asset="Current Pegged Token" value={'DSD'} suffix={""} />
          </Grid>
    </Grid.Container>
    </>
  );
}

export default Stats;
