import React, { useEffect, useState } from 'react'
import Sidebar from './sidebar'
import TabbarMobile from './sidebar/tabbar-mobile'
import { useTheme, useMediaQuery} from '@geist-ui/react'
import ConnectButton from './sidebar/ConnectButton';
import { useHistory } from 'react-router-dom';
import _JSXStyle from "styled-jsx/style"

export interface Props {
  hasWeb3: boolean,
  user: string,
  setUser: Function,
  switchTheme: Function,
  getStaticProps?: any
}

export const Layout: React.FC<React.PropsWithChildren<Props>> = React.memo((props, { children }) => {
  const [expanded, setExpanded] = useState<boolean>(false)
  const theme = useTheme()
  const history = useHistory()
  const mobileTabbarClickHandler = () => {
    setExpanded(!expanded)
  }
  const logoUrl = `./logo/logo.jpeg` 
  const upSM = useMediaQuery('sm', { match: 'up' })
  useEffect(() => {})

  return (
    <div className="layout">
      <TabbarMobile onClick={mobileTabbarClickHandler} connectButton={<ConnectButton hasWeb3={props.hasWeb3} user={props.user} setUser={props.setUser} />} />
      <aside className="sidebar">
        <div style={{
                margin: '10px'
              }}>
          <a onClick={() => history.push('/')} style={{
            marginRight: '16px', 
            height: '40px',
            // background: '#0a133e'
            
            }}>

            <img src={logoUrl} style={{
              borderRadius: '50%',
            }} height="25px" alt="Variable Timing Dollar"/>
          </a>
          <small style={{
            position: 'absolute',
            top: '10px'
          }}
          
        >VTD</small>
        </div>
        {upSM ? <ConnectButton hasWeb3={props.hasWeb3} user={props.user} setUser={props.setUser} /> : <></>}
        <Sidebar hasWeb3={props.hasWeb3} user={props.user} setUser={props.setUser} closeSideBar={mobileTabbarClickHandler} switchTheme={props.switchTheme}/>
      </aside>
      <div className="side-shadow" />
      <main className="main">
       {props.children}
      </main>
      {/* @ts-ignore */}
      <style jsx>{`
        .layout {
          min-height: calc(100vh - 108px);
          max-width: 100vw;
          margin: 0 auto;
          padding: 0 ${theme.layout.gap};
          display: flex;
          box-sizing: border-box;
          background: ${theme.palette.background};
        }

        .sidebar {
          width: 120px;
          margin-right: 20px;
          -webkit-overflow-scrolling: touch;
          -webkit-flex-shrink: 0;
          height: calc(100% - 2rem - 10px + ${false ? '60px' : 0});
          position: fixed;
          top: 10px;
          bottom: 2rem;
          transform: translateY(${false ? '-60px' : 0});
          transition: transform 200ms ease-out;
          z-index: 100;
        }

        .side-shadow {
          width: 140px;
          flex-shrink: 0;
          height: 100vh;
        }

        .main {
          display: flex;
          max-width: calc(100% - 140px);
          flex-direction: column;
          padding-left: 20px;
          padding-top: 25px;
          flex: 0 0 100%;
          padding-bottom: 150px;
        }

        @media only screen and (max-width: ${theme.layout.breakpointMobile}) {
          .layout {
            max-width: 100%;
            width: 100%;
            padding: 5rem 1rem;
          }

          .sidebar {
            position: fixed;
            top: 0;
            left: 0;
            right: 0;
            z-index: 10;
            width: 100vw;
            height: ${expanded ? '100vh' : '0'};
            background-color: ${theme.palette.background};
            padding: 0;
            overflow: hidden;
            transition: height 250ms ease;
          }

          .main {
            width: 90vw;
            max-width: 90vw;
            padding: 0;
          }

          .side-shadow {
            display: none;
            visibility: hidden;
          }
        }
      `}</style>
    </div>
  )
})

export default Layout
