import React from 'react';
import {VTD, VTDD, PeggedTokens} from "../constants/tokens";
import BigNumber from 'bignumber.js';
import {
  getPoolBalanceOfBonded, getPoolBalanceOfClaimable,
  getPoolBalanceOfRewarded,
  getPoolBalanceOfStaged,
  getPoolStatusOf, getPoolTotalBonded,
  getTokenAllowance,
  getTokenBalance,
  getPoolFluidUntil,
  getPoolDistributions,
  getInstantaneousPrice,
  getPoolTotalClaimable, getPoolTotalRewarded,
  getTokenTotalSupply, getTotalBonded, getTotalCoupons, getTotalDebt, getTotalRedeemable, getTotalStaged,
  getEpoch, getEpochTime, getNextEpochTimestamp, getCurrentEpochLength, getAllRegulations
} from '../utils/infura';
import { toTokenUnitsBN } from '../utils/number';

export type ContextType = {
  regulations: IEpochs[],
  epochData: EpochData,
  updateEpochData: () => void,
  updateRegulations: () => void,
}

export const EpochContext = React.createContext<ContextType | null>(null);

export type EpochData = {
  currentEpochLength: number,
  nextEpochTimestamp: number,
  previousEpochTimestamp: number,
}
export type IEpochs = {
    type: string;
    epoch: number;
    price: BigNumber;
    momentumPrice?: BigNumber;
    deltaRedeemable: BigNumber;
    deltaDebt: BigNumber;
    deltaBonded: BigNumber;
  }
  
const initialEpochData = {
  currentEpochLength: 0,
  nextEpochTimestamp: 0,
  previousEpochTimestamp: 0,
}

export const EpochStoreProvider: React.FC<React.ReactNode> = ({ children }) => {
  const [regulations, setRegulations] = React.useState<IEpochs[]>([]);
  const [epochData, setEpochData] = React.useState<EpochData>(initialEpochData);

  const updateRegulations = async () => {
    const [allRegulations] = await Promise.all([
        getAllRegulations(VTDD.addr),
      ]);

      const _regs = allRegulations.map((result, index) => {
        const data = result.data;
        const epochtype = result.type;
        return {
            type: epochtype,
            // @ts-ignore
            epoch: Number(data.epoch),
            // @ts-ignore
            price: toTokenUnitsBN(data.price, VTD.decimals),
            // @ts-ignore
            momentumPrice: data.momentumPrice ? toTokenUnitsBN(data.momentumPrice, VTD.decimals) : new BigNumber(0),
            // @ts-ignore
            deltaRedeemable: toTokenUnitsBN(data.newRedeemable, VTD.decimals),
            // @ts-ignore
            deltaDebt: epochtype === 'INCREASE' ? toTokenUnitsBN(data.lessDebt, VTD.decimals) : toTokenUnitsBN(data.newDebt, VTD.decimals),
            // @ts-ignore
            deltaBonded: toTokenUnitsBN(data.newBonded, VTD.decimals)
        }
      })
      setRegulations(_regs)
    }
    const updateEpochData = async () => {
      const [nextEpochTimestampStr, currentEpochLengthStr] = await Promise.all([
        getNextEpochTimestamp(VTDD.addr),
        getCurrentEpochLength(VTDD.addr)
      ]);
      const nextEpochTimestamp = parseInt(nextEpochTimestampStr, 10);
      const currentEpochLength= parseInt(currentEpochLengthStr, 10);
      const previousEpochTimestamp = nextEpochTimestamp - currentEpochLength;

      setEpochData({
        currentEpochLength: currentEpochLength,
        nextEpochTimestamp: nextEpochTimestamp,
        previousEpochTimestamp: previousEpochTimestamp,
      })
    }

  return (
    <EpochContext.Provider value={{regulations, epochData, updateEpochData, updateRegulations}}>
      {children}
    </EpochContext.Provider>
  );
};

export default EpochStoreProvider;
