import React, { useState, useEffect } from 'react';
import { useParams } from 'react-router-dom';

import BigNumber from 'bignumber.js';
import {
  getBalanceBonded,
  getBalanceOfStaged, getFluidUntil, getLockedUntil,
  getStatusOf, getTokenAllowance,
  getTokenBalance, getTokenTotalSupply,
} from '../../utils/infura';
import {VTD, VTDD} from "../../constants/tokens";
import { toTokenUnitsBN } from '../../utils/number';

import AccountPageHeader from "./Header";
import Pool from "../Pool";
import EmptyPool from "../Pool/EmptyPool";

import WithdrawDeposit from "./WithdrawDeposit";
import BondUnbond from "./BondUnbond";
import MomentumHeader from "./MomentumHeader";

import EpochDetail from "../EpochDetail";
import Overlay from '../layout/overlay'
import { useTheme, Grid, Card, Button, Divider, Page } from '@geist-ui/react'

import { MainContext, ContextType } from "../../store/MainStore";

function Wallet({ user }: {user: string}) {
  const { override } = useParams();
  if (override) {
    user = override;
  }
  const theme = useTheme();
  const [sidePanelOpened, setSidePanelOpened] = useState(false)
  const [userVTDBalance, setUserVTDBalance] = useState(new BigNumber(0));
  const [userVTDAllowance, setUserVTDAllowance] = useState(new BigNumber(0));
  const [userVTDSBalance, setUserVTDSBalance] = useState(new BigNumber(0));
  const [totalVTDSSupply, setTotalVTDSSupply] = useState(new BigNumber(0));
  const [userStagedBalance, setUserStagedBalance] = useState(new BigNumber(0));
  const [userBondedBalance, setUserBondedBalance] = useState(new BigNumber(0));
  const [userStatus, setUserStatus] = useState(0);
  const [userStatusUnlocked, setUserStatusUnlocked] = useState(0);
  const [lockup, setLockup] = useState(0);
  const [selectedTab, setSelectedTab] = useState(0)
  
  const { peggedTokens, updatePools } = React.useContext(MainContext) as ContextType;

  //Update User balances
  useEffect(() => {
    if (user === '') {
      setUserVTDBalance(new BigNumber(0));
      setUserVTDAllowance(new BigNumber(0));
      setUserVTDSBalance(new BigNumber(0));
      setTotalVTDSSupply(new BigNumber(0));
      setUserStagedBalance(new BigNumber(0));
      setUserBondedBalance(new BigNumber(0));
      setUserStatus(0);
      return;
    }
    let isCancelled = false;

    async function updateUserInfo() {
      const [
        vtdBalance, vtdAllowance, vtdsBalance, vtdsSupply, stagedBalance, bondedBalance, status,
        fluidUntilStr, lockedUntilStr
      ] = await Promise.all([
        getTokenBalance(VTD.addr, user),
        getTokenAllowance(VTD.addr, user, VTDD.addr),
        getTokenBalance(VTDD.addr, user),
        getTokenTotalSupply(VTDD.addr),
        getBalanceOfStaged(VTDD.addr, user),
        getBalanceBonded(VTDD.addr, user),
        getStatusOf(VTDD.addr, user),

        getFluidUntil(VTDD.addr, user),
        getLockedUntil(VTDD.addr, user),
      ]);

      const userVTDBalance = toTokenUnitsBN(vtdBalance, VTD.decimals);
      const userVTDSBalance = toTokenUnitsBN(vtdsBalance, VTDD.decimals);
      const totalVTDSSupply = toTokenUnitsBN(vtdsSupply, VTDD.decimals);
      const userStagedBalance = toTokenUnitsBN(stagedBalance, VTDD.decimals);
      const userBondedBalance = toTokenUnitsBN(bondedBalance, VTDD.decimals);
      const userStatus = parseInt(status, 10);
      const fluidUntil = parseInt(fluidUntilStr, 10);
      const lockedUntil = parseInt(lockedUntilStr, 10);

      if (!isCancelled) {
        setUserVTDBalance(new BigNumber(userVTDBalance));
        setUserVTDAllowance(new BigNumber(vtdAllowance));
        setUserVTDSBalance(new BigNumber(userVTDSBalance));
        setTotalVTDSSupply(new BigNumber(totalVTDSSupply));
        setUserStagedBalance(new BigNumber(userStagedBalance));
        setUserBondedBalance(new BigNumber(userBondedBalance));
        setUserStatus(userStatus);
        setUserStatusUnlocked(Math.max(fluidUntil, lockedUntil))
        // setLockup(poolAddress === DollarPool4 ? DAO_EXIT_LOCKUP_EPOCHS : 1);
      }
    }
    updateUserInfo();
    const id = setInterval(updateUserInfo, 10000);
    updatePools();

    // eslint-disable-next-line consistent-return
    return () => {
      isCancelled = true;
      clearInterval(id);
    };
  }, [user]);


  return (
    <>
    <Grid.Container gap={2} justify="center">
      <Grid xs={24} md={18}>
         <MomentumHeader user={user}/> 
         <Card type="lite" style={{margin: '10px 0', background: `${theme.palette.accents_2}`}}>
            DAO
            <Divider y={0} />
            <AccountPageHeader
              accountVTDBalance={userVTDBalance}
              accountVTDSBalance={userVTDSBalance}
              totalVTDSSupply={totalVTDSSupply}
              accountStagedBalance={userStagedBalance}
              accountBondedBalance={userBondedBalance}
              accountStatus={userStatus}
              unlocked={userStatusUnlocked}
            />
            <Divider y={0} />

                  <Button
                  // icon={<IconPlus />}
                  onClick={() => setSidePanelOpened(true)}
                >Bond/Unbond Tokens </Button>

          </Card>

          {peggedTokens.map((peg, index) => 
            peg.enabled ? <Pool user={user} peg={peg} /> : <EmptyPool peg={peg} />
          )}
      </Grid>
      <Grid xs={24} md={6}>
      <EpochDetail user={user}/>
      
      </Grid>
    </Grid.Container>
            
      
      <Overlay
        visible={sidePanelOpened}
        onClose={() => setSidePanelOpened(false)}
      >
        <Page>
    
            <WithdrawDeposit
            user={user}
            balance={userVTDBalance}
            allowance={userVTDAllowance}
            stagedBalance={userStagedBalance}
            status={userStatus}
          />
    
          <BondUnbond
            staged={userStagedBalance}
            bonded={userBondedBalance}
            status={userStatus}
            lockup={lockup}
          />

        
      </Page>
      </Overlay>
    </>
  );
}

export default Wallet;
