import React, { useState } from 'react';
import { useWallet } from 'use-wallet';
import { LogOut } from '@geist-ui/react-icons'
import { Button, Spacer, useMediaQuery } from '@geist-ui/react'

import {
  IdentityBadge, IconConnect, LinkBase,
} from '@aragon/ui';

import { connect } from '../../../utils/web3';
// import TotalBalance from "./TotalBalance";
import ConnectModal from './ConnectModal';

type connectButtonProps = {
  hasWeb3: boolean,
  user: string,
  setUser: Function
}

function ConnectButton({ hasWeb3, user, setUser }: connectButtonProps) {
  const { status, reset } = useWallet();

  const [isModalOpen, setModalOpen] = useState(false);
  const downSM = useMediaQuery('sm', { match: 'down' })

  const connectWeb3 = async (wallet) => {
    connect(wallet.ethereum);
    setUser(wallet.account);
  };

  const disconnectWeb3 = async () => {
    setUser('');
    reset();
  };

  const toggleModal = () => setModalOpen(!isModalOpen);

  return status === 'connected' ? (
    <div style={{display: 'flex', flexDirection: 'column', minHeight: '5rem', justifyContent: 'space-evenly'}}>
      {downSM ? <></> :<IdentityBadge entity={user} /> }
      <LinkBase onClick={disconnectWeb3} style={{marginRight: '8px', height: '24px'}}>
        <Button shadow icon={<LogOut />} auto size="small">Log Out</Button>
      </LinkBase>
    </div>
  ) : (
    <>
      <ConnectModal visible={isModalOpen} onClose={toggleModal} onConnect={connectWeb3}/>
      <Button icon={<IconConnect />} onClick={toggleModal} disabled={!hasWeb3} auto size="small"> Connect</Button>
    </>
  );
}


export default ConnectButton;
