import React, { useState, useEffect } from 'react';
import { useParams } from 'react-router-dom';

import BigNumber from 'bignumber.js';
import {
  getPoolBalanceOfBonded, getPoolBalanceOfClaimable,
  getPoolBalanceOfRewarded,
  getPoolBalanceOfStaged,
  getPoolStatusOf, getPoolTotalBonded,
  getTokenAllowance,
  getTokenBalance,
  getPoolFluidUntil,
  getPoolDistributions
} from '../../utils/infura';
import {VTD, VTDD} from "../../constants/tokens";
import {formatBN} from "../../utils/number";
import {POOL_EXIT_LOCKUP_EPOCHS} from "../../constants/values";
import { toTokenUnitsBN } from '../../utils/number';
import { SidePanel } from '@aragon/ui';
import { ButtonGroup, Button, Card, Divider, useTheme, Grid, useMediaQuery, Link } from '@geist-ui/react'
import { X } from '@geist-ui/react-icons'

import WithdrawDeposit from "./WithdrawDeposit";
import BondUnbond from "./BondUnbond";
import PoolPageHeader from "./Header";
import Claim from "./Claim";
import Provide from "./Provide";
import { MainContext, ContextType } from "../../store/MainStore";

// import Migrate from "./Migrate";
// import {DollarPool4} from "../../constants/contracts";

type PoolProps = {user: string, peg: any};

function Pool({ user, peg }: PoolProps) {
  const { override } = useParams();
  if (override) {
    user = override;
  }
  const theme = useTheme();
  const [poolAddress, setPoolAddress] = useState("");
  const [userUNIBalance, setUserUNIBalance] = useState(new BigNumber(0));
  const [userUNIAllowance, setUserUNIAllowance] = useState(new BigNumber(0));
  const [userPEGBalance, setUserPEGBalance] = useState(new BigNumber(0));
  const [userPEGAllowance, setUserPEGAllowance] = useState(new BigNumber(0));
  const [userStagedBalance, setUserStagedBalance] = useState(new BigNumber(0));
  const [userBondedBalance, setUserBondedBalance] = useState(new BigNumber(0));
  const [userRewardedBalance, setUserRewardedBalance] = useState(new BigNumber(0));
  const [userClaimableBalance, setUserClaimableBalance] = useState(new BigNumber(0));
  const [userStatus, setUserStatus] = useState(0);
  const [userStatusUnlocked, setUserStatusUnlocked] = useState(0);
  const [lockup, setLockup] = useState(0);
  const [tokensPanelOpened, setTokensPanelOpened] = useState(false)
  const [rewardPanelOpened, setRewardPanelOpened] = useState(false)
  const { updatePools } = React.useContext(MainContext) as ContextType;
  const downMD = useMediaQuery('sm', { match: 'down' })

  function fm(bn){
    let num = (formatBN(bn, 2))
    return num;
  }
  //Update User balances
  useEffect(() => {
    if (user === '') {
      setPoolAddress("");
      setUserUNIBalance(new BigNumber(0));
      setUserUNIAllowance(new BigNumber(0));
      setUserPEGBalance(new BigNumber(0));
      setUserPEGAllowance(new BigNumber(0));
      setUserStagedBalance(new BigNumber(0));
      setUserBondedBalance(new BigNumber(0));
      setUserRewardedBalance(new BigNumber(0));
      setUserClaimableBalance(new BigNumber(0));
      setUserStatus(0);
      setUserStatusUnlocked(0);
      
      return;
    }
    let isCancelled = false;

    async function updateUserInfo() {
      const poolAddressStr = await peg.pool;

      const [
        balance, usdcBalance,
        allowance, usdcAllowance, stagedBalance, bondedBalance,
        rewardedBalance, claimableBalance, status, fluidUntilStr
      ] = await Promise.all([
        getTokenBalance(peg.pair.addr, user),
        getTokenBalance(peg.addr, user),

        getTokenAllowance(peg.pair.addr, user, poolAddressStr),
        getTokenAllowance(peg.addr, user, poolAddressStr),
        getPoolBalanceOfStaged(poolAddressStr, user),
        getPoolBalanceOfBonded(poolAddressStr, user),

        getPoolBalanceOfRewarded(poolAddressStr, user),
        getPoolBalanceOfClaimable(poolAddressStr, user),
        getPoolStatusOf(poolAddressStr, user),
        getPoolFluidUntil(poolAddressStr, user),
        // getPoolDistributions(VTDD.address)
      ]);
      
      const userUNIBalance = toTokenUnitsBN(balance, peg.pair.decimals);
      const userPEGBalance = toTokenUnitsBN(usdcBalance, peg.decimals);
      const userStagedBalance = toTokenUnitsBN(stagedBalance, peg.pair.decimals);
      const userBondedBalance = toTokenUnitsBN(bondedBalance, peg.pair.decimals);
      const userRewardedBalance = toTokenUnitsBN(rewardedBalance, VTD.decimals);
      const userClaimableBalance = toTokenUnitsBN(claimableBalance, VTD.decimals);
      const userStatus = parseInt(status, 10);
      const fluidUntil = parseInt(fluidUntilStr, 10);

      if (!isCancelled) {
        setPoolAddress(poolAddressStr);
        setUserUNIBalance(new BigNumber(userUNIBalance));
        setUserUNIAllowance(new BigNumber(allowance));
        setUserPEGAllowance(new BigNumber(usdcAllowance));
        setUserPEGBalance(new BigNumber(userPEGBalance));
        setUserStagedBalance(new BigNumber(userStagedBalance));
        setUserBondedBalance(new BigNumber(userBondedBalance));
        setUserRewardedBalance(new BigNumber(userRewardedBalance));
        setUserClaimableBalance(new BigNumber(userClaimableBalance));
        setUserStatus(userStatus);
        setUserStatusUnlocked(fluidUntil);
        // setLockup(poolAddressStr === DollarPool4 ? POOL_EXIT_LOCKUP_EPOCHS : 1);
      }
    }
    updateUserInfo();
    const id = setInterval(updateUserInfo, 15000);

    // eslint-disable-next-line consistent-return
    return () => {
      isCancelled = true;
      clearInterval(id);
    };
  }, [user]);
  // Check for error in .call()
  return (
    <>
      <Card type="lite" style={{margin: '10px 0', background: `${theme.palette.accents_1}`}}>
        <Grid.Container gap={2} justify="center">
          <Grid xs={12} md={12}>
            {peg.symbol}/
            <div style={{fontSize: '10px'}}>
              VTD
            </div> 
          </Grid>
          <Grid xs={12} md={4}>
            <div>
            Price: 
            </div>
            <div style={{fontSize: '10px'}}>
              {fm(peg.stats.price)} {peg.symbol} / {fm(peg.stats.price.multipliedBy(peg.usdcPrice))} USD 
            </div>  
          </Grid>
          <Grid xs={12} md={4}>
            <div>
            VTD Pooled: 
            </div>
            <div style={{fontSize: '12px'}}>
              {fm(peg.stats.vtdBalance)}
            </div>   
          </Grid>
          <Grid xs={12} md={4}>
            <div>
            {peg.symbol} Pooled: 
            </div>
            <div style={{fontSize: '12px'}}>
              {fm(peg.stats.pegBalance)}
            </div>  
          </Grid>
          <Divider y={0} />
          
            <PoolPageHeader
              accountUNIBalance={userUNIBalance}
              accountStagedBalance={userStagedBalance}
              accountBondedBalance={userBondedBalance}
              accountRewardedVTDBalance={userRewardedBalance}
              accountClaimableVTDBalance={userClaimableBalance}
              poolTotalBonded={peg.stats.poolTotalBonded}
              accountPoolStatus={userStatus}
              unlocked={userStatusUnlocked}
            />
          <Grid xs>
            <ButtonGroup size={downMD? 'mini' : 'medium'} type="success" ghost>
              <Button
              type="success"
                onClick={() => setTokensPanelOpened(true)}
              >Bond/Unbond Tokens</Button>
              <Button
              type="success"
              // icon={<IconPlus />}
              onClick={() => setRewardPanelOpened(true)}
            >Claim/Provide Rewards</Button>
            </ButtonGroup>
          </Grid>
          <Grid xs>
            <ButtonGroup size={downMD? 'mini' : 'medium'} ghost>
            <Button 
                onClick={() => window.open(`https://app.uniswap.org/#/swap?inputCurrency=${peg.addr}&outputCurrency=${VTD.addr}`, '_blank')}
                ghost>Trade VTD/{peg.symbol}</Button>
              <Button 
              // icon={<IconPlus />}
              onClick={() => window.open(`https://app.uniswap.org/#/add/${VTD.addr}/${peg.addr}`, '_blank')}
              ghost>Add Uniswap Liquidity</Button>
            </ButtonGroup>
          </Grid>
          </Grid.Container>
        </Card>
        <SidePanel
          title="Add/Remove Liquidity"
          opened={tokensPanelOpened}
          onClose={() => setTokensPanelOpened(false)}
        >
          <Link onClick={() => setTokensPanelOpened(false)}><X /></Link>
          <WithdrawDeposit
          poolAddress={poolAddress}
          user={user}
          balance={userUNIBalance}
          allowance={userUNIAllowance}
          stagedBalance={userStagedBalance}
          status={userStatus}
          peg={peg}
        />

        <BondUnbond
          poolAddress={poolAddress}
          staged={userStagedBalance}
          bonded={userBondedBalance}
          status={userStatus}
          lockup={lockup}
          peg={peg}
        />
    
        
      </SidePanel>

      <SidePanel
        title="Claim/Zap Rewards"
        opened={rewardPanelOpened}
        onClose={() => setRewardPanelOpened(false)}
      >
          <Link onClick={() => setRewardPanelOpened(false)}><X /></Link>
        
        <Claim
          poolAddress={poolAddress}
          claimable={userClaimableBalance}
          status={userStatus}
        />

        <Provide
          poolAddress={poolAddress}
          user={user}
          rewarded={userRewardedBalance}
          status={userStatus}
          pairBalanceVTD={peg.stats.vtdBalance}
          pairBalancePeg={peg.stats.pegBalance}
          userPEGBalance={userPEGBalance}
          userPEGAllowance={userPEGAllowance}
          peg={peg}
        />
        
      </SidePanel>
    </>
  );
}

export default Pool;
