import React, { useState, useCallback } from 'react';
import { LoadingRing } from '@aragon/ui'
import { Button} from '@geist-ui/react'
import {waitTransaction} from '../../../utils/txStatus'
import {
  TransactionProgress
} from '@aragon/ui';

function PendingTxButton() {
    const [pendingTxVisible, setPendingTxVisible] = useState(false);
    const [pendingTxStr, setPendingTxStr] = useState('');
    const [opener, setOpener] = useState(null);
    const handleOpenerRef = useCallback(node => {
      setOpener(node)
    }, [])
    
    async function tick(){
        const _tx = localStorage.getItem('pendingtx')
        if (_tx) {
            setPendingTxStr(_tx);
            var status = await waitTransaction(_tx);
            console.log(status)
            localStorage.removeItem('pendingtx');
        } else {
            setPendingTxStr('');
            setPendingTxVisible(false);
        }
    };

    setInterval(
    () => tick(),
    5000
    );
  return (
    <div style={{display: 'flex', margin:'0 auto'}}>
        {pendingTxStr ? 
            <Button size='mini' auto icon={<LoadingRing size={10}/>} type='warning' ref={handleOpenerRef} onClick={() => setPendingTxVisible(true)}>Pending Tx</Button>
        : ''}

            <TransactionProgress
              transactionHashUrl={'https://etherscan.io/tx/' + pendingTxStr}
              visible={pendingTxVisible}
              onClose={() => setPendingTxVisible(false)}
              opener={opener}
            />
    </div>
  );
}


export default PendingTxButton;
