import React, { useEffect, useState } from 'react'
import { useTheme, Link } from '@geist-ui/react'
import _JSXStyle from "styled-jsx/style"
import { X } from '@geist-ui/react-icons'


interface Props {
  visible: boolean
  onClose?: () => void
}

const Overlay: React.FC<React.PropsWithChildren<Props>> = ({
  visible: customVisible,
  onClose,
  children,
}) => {
  const theme = useTheme()
  const [visible, setVisible] = useState<boolean>(false)

  useEffect(() => {
    if (customVisible !== undefined) {
      setVisible(customVisible)
    }
  }, [customVisible])

  const clickHandler = (e) => {
    setVisible(false)
    onClose && onClose()
    e.preventDefault()

  }
  return (
    <section className={visible ? 'active' : ''}>
      <Link className='overlayClose' onClick={clickHandler} style={{position: 'fixed', right: '20px', top: '10px', zIndex: 5001}}><X /></Link>
      {children}
      <style jsx>{`
        section {
          position: fixed;
          width: 100vw;
          height: 100vh;
          background-color: ${theme.palette.background};
          z-index: 5000;
          top: -5000px;
          left: -5000px;
          display: none;
          overflow-y: scroll;
        }

        .active {
          top: 0;
          left: 0;
          bottom: 0;
          display: block;
        }
      `}</style>
    </section>
  )
}

export default Overlay
