import React, {useEffect, useState, useContext} from 'react';

import { HashRouter as Router, Switch, Route } from 'react-router-dom';
import { Root, Viewport, Theme } from '@aragon/ui';
import { UseWalletProvider } from 'use-wallet';
import { updateModalMode } from './utils/web3';
import { storePreference, getPreference } from './utils/storage';
import NavBar from './components/NavBar';
import Wallet from "./components/Wallet";
import Stats from "./components/Stats";
// import SellPressure from "./components/Stats/sellpressure";
import EpochDetail from "./components/EpochDetail";
import CouponMarket from "./components/CouponMarket";
import Governance from "./components/Governance";
import Candidate from "./components/Candidate";
import Regulation from "./components/Regulation";
import Pool from "./components/Pool";
import HomePageNoWeb3 from "./components/HomePageNoWeb3";
import {vtdTheme, vtdLight} from "./theme/";
import {MainStoreProvider} from "./store/MainStore";
import {EpochStoreProvider} from "./store/EpochStore";
import { ButtonGroup, Button, GeistProvider, Card, Divider, CssBaseline, Page } from '@geist-ui/react'
import Layout from "./components/layout/layout"


function App() {
  const [hasWeb3, setHasWeb3] = useState(false);
  const [user, setUser] = useState(''); // the current connected user
  const [themeType, setThemeType] = useState(vtdTheme)
  const switchThemes = () => {
    // @ts-ignore
    setThemeType(lastThemeType => (lastThemeType.name === 'vtdDark' ? vtdLight : vtdTheme))
  }

  useEffect(() => {
    let isCancelled = false;

    async function updateUserInfo() {
      if (!isCancelled) {
        // @ts-ignore
        setHasWeb3(typeof window.ethereum !== 'undefined');
      }
    }

    updateUserInfo();
    const id = setInterval(updateUserInfo, 15000);

    // eslint-disable-next-line consistent-return
    return () => {
      isCancelled = true;
      clearInterval(id);
    };
  }, [user]);

  return (
    <GeistProvider theme={themeType} >
    <Router>
      <UseWalletProvider
        chainId={1}
        connectors={{
          walletconnect: { rpcUrl: 'https://mainnet.eth.aragon.network/' },
          walletlink: {
            url: 'https://mainnet.eth.aragon.network/',
            appName:'Coinbase Wallet',
            appLogoUrl: ''
          }
        }}
      >
        {/* @ts-ignore */}
        <MainStoreProvider><EpochStoreProvider>
            <CssBaseline />

            <Root.Provider>
              <Viewport.Provider>
                <Layout hasWeb3={hasWeb3} user={user} setUser={setUser} switchTheme={switchThemes}>
                  <Theme theme={themeType.name === 'vtdDark' ? 'dark' : 'light'}>
                  {
                    hasWeb3 ?
                      <Switch>
                        <Route path="/dao/:override"><Wallet user={user}/></Route>
                        <Route path="/dao/"><Wallet user={user}/></Route>
                        <Route path="/epoch/"><EpochDetail user={user}/></Route>
                        <Route path="/coupons/:override"><CouponMarket user={user}/></Route>
                        <Route path="/coupons/"><CouponMarket user={user}/></Route>
                        <Route path="/governance/candidate/:candidate"><Candidate user={user}/></Route>
                        <Route path="/governance/"><Governance user={user}/></Route>
                        {/* <Route path="/trade/"><Trade user={user}/></Route> */}
                        <Route path="/supply/"><Regulation user={user}/></Route>
                        {/* <Route path="/pool/:override"><Pool user={user}/></Route>
                        <Route path="/pool/"><Pool user={user}/></Route> */}
                        <Route path="/tools/"><Stats user={user}/></Route>
                        {/* <Route path="/bondstats/"><SellPressure/></Route> */}

                        <Route path="/"><Wallet user={user}/></Route>
                      </Switch>
                      :
                      <Switch>
                        <Route path="/"><HomePageNoWeb3/></Route>
                      </Switch>
                  }
                  </Theme>
                </Layout>
              </Viewport.Provider>
            </Root.Provider>
        </EpochStoreProvider></MainStoreProvider>
      </UseWalletProvider>
    </Router>
    </GeistProvider>
  );
}


export default App;
