import React from 'react';
import BigNumber from 'bignumber.js';

import { BalanceBlock,StatsRowItem } from '../common/index';

import {Grid} from '@geist-ui/react'
import {ownership} from "../../utils/number";
import {formatBN} from "../../utils/number";

type PoolPageHeaderProps = {
  accountUNIBalance: BigNumber,
  accountStagedBalance: BigNumber,
  accountBondedBalance: BigNumber,
  accountRewardedVTDBalance: BigNumber,
  accountClaimableVTDBalance: BigNumber,
  poolTotalBonded: BigNumber,
  accountPoolStatus: number,
  unlocked: number,
};

const STATUS_MAP = ["Unlocked", "Locked"];

function status(accountStatus, unlocked) {
  return STATUS_MAP[accountStatus] + (accountStatus === 0 ? "" : " until " + unlocked)
}

function fm(bn){
  let num = (formatBN(bn, 2))
  return num;
}



const PoolPageHeader = ({
  accountUNIBalance, accountStagedBalance, accountBondedBalance, accountRewardedVTDBalance, accountClaimableVTDBalance, poolTotalBonded, accountPoolStatus, unlocked
}: PoolPageHeaderProps) => (
  <>
    <Grid xs={24} sm={12}>
        <StatsRowItem asset="Balance" value={fm(accountUNIBalance)} suffix={" UNI-V2"}/>
        <StatsRowItem asset="Staged" value={fm(accountStagedBalance)}  suffix={" UNI-V2"}/>
        <StatsRowItem asset="Bonded" value={fm(accountBondedBalance)}  suffix={" UNI-V2"}/>
     </Grid>
     <Grid xs={24} sm={12}>

            <StatsRowItem asset="Rewarded" value={fm(accountRewardedVTDBalance)}  suffix={" VTD"}/>
            <StatsRowItem asset="Claimable" value={fm(accountClaimableVTDBalance)}  suffix={" VTD"}/>
            <StatsRowItem asset="Pool Ownership" value={fm(ownership(accountBondedBalance, poolTotalBonded))} suffix={"%"} />
            <StatsRowItem asset="Pool status" value={status(accountPoolStatus, unlocked)}  suffix={""}/>
    </Grid>
  </>
);


export default PoolPageHeader;
