import React, { useMemo, useEffect, useState } from 'react'
import { useTheme } from '@geist-ui/react'
import { useHistory } from 'react-router-dom';
import _JSXStyle from "styled-jsx/style"

export interface Props {
  href: string
  text: string
  closeSideBar: Function
}

const ActiveLink: React.FC<Props> = React.memo(({ href, text, closeSideBar }) => {
  const [page, setPage] = useState("");
  const theme = useTheme()
  const history = useHistory()
  const [title, subtitle] = useMemo(() => {
    if (!/[\u4E00-\u9FA5]/.test(text)) return [text, null]
    if (/zeit|ui|ZEIT|geist|Geist|UI/.test(text)) return [text, null]
    return [text.replace(/[^\u4E00-\u9FA5]/g, ''), text.replace(/[^a-zA-Z]/g, '')]
  }, [text])

  useEffect(() => {
    return history.listen((location) => {
      setPage(location.pathname)
    })
  }, [history]);
  
  return (
    <div className="link">
        <a onClick={() => {closeSideBar(); history.push(href)}} className={page.includes(href) ? 'active' : ''}>
          {title}
          {subtitle && <span>&nbsp;{subtitle}</span>}
        </a>

      <style jsx>{`
        .link {
          width: 100%;
          color: ${theme.palette.accents_5};
          display: flex;
          height: 2.25rem;
          align-items: center;
          justify-content: center;
          cursor: pointer;
          text-transform: capitalize;
          padding-bottom: 1rem;
        }

        a {
          color: ${theme.palette.accents_7};
          font-size: 1rem;
          transition: all 200ms ease;
          font-weight: 400;
          display: inline-flex;
          align-items: baseline;
        }

        a.active {
          color: ${theme.palette.success};
          font-weight: 600;
        }

        a.active span {
          color: ${theme.palette.successLight};
        }

        span {
          font-size: 0.75rem;
          color: ${theme.palette.accents_4};
          font-weight: 400;
        }
      `}</style>
    </div>
  )
})

export default ActiveLink
