import React, {useEffect, useState, useContext} from 'react';
import { DataView } from '@aragon/ui';

import {getAllRegulations} from '../../utils/infura';
import {VTD, VTDD} from "../../constants/tokens";
import {formatBN, toTokenUnitsBN} from "../../utils/number";
import BigNumber from "bignumber.js";
import {EpochContext, ContextType, IEpochs } from "../../store/EpochStore";

type RegulationHistoryProps = {
  user: string,
};

type Regulation = {
  type: string,
  data: RegulationEntry
}

type RegulationEntry = {
  epoch: string;
  price: string;
  momentumPrice: string;
  deltaRedeemable: string;
  deltaDebt: string;
  deltaBonded: string;
}

function formatPrice(reg) {
  return reg.type === 'NEUTRAL' ? '1.00' : formatBN(reg.price, 2)
}

function formatMomentumPrice(reg) {
  return !!reg.momentumPrice.toNumber() ? formatBN(reg.momentumPrice, 2) : '-'
}
function formatDeltaRedeemable(reg) {
  return reg.type === 'INCREASE' ?
    '+' + formatBN(reg.deltaRedeemable, 2) :
    '+0.00';
}

function formatDeltaDebt(reg) {
  return reg.type === 'INCREASE' ?
    '-' + formatBN(reg.deltaDebt, 2) :
    reg.type === 'DECREASE' ?
      '+' + formatBN(reg.deltaDebt, 2) :
      '+0.00';
}

function formatDeltaBonded(reg) {
  return reg.type === 'INCREASE' ?
    '+' + formatBN(reg.deltaBonded, 2) :
    '+0.00';
}

function renderEntry(reg: IEpochs): string[] {
  return [
    reg.epoch.toString(),
    formatPrice(reg),
    formatMomentumPrice(reg),
    formatDeltaRedeemable(reg),
    formatDeltaDebt(reg),
    formatDeltaBonded(reg),
  ]
}

function RegulationHistory({
  user,
}: RegulationHistoryProps) {
  const [page, setPage] = useState(0)
  const [initialized, setInitialized] = useState(false)
  const { regulations, updateRegulations} = React.useContext(EpochContext) as ContextType;

  //Update User balances
  useEffect(() => {
    let isCancelled = false;

    async function updateUserInfo() {
      updateRegulations();
    }

    updateUserInfo();
    const id = setInterval(updateUserInfo, 15000);

    // eslint-disable-next-line consistent-return
    return () => {
      isCancelled = true;
      clearInterval(id);
    };
  }, [user]);

  return (
    <DataView
      fields={['Epoch', 'Price', 'Momentum Price', ' Redeemable', ' Debt', ' Bonded']}
      status={ initialized ? 'default' : 'loading' }
      entries={regulations}
      entriesPerPage={10}
      page={page}
      onPageChange={setPage}
      renderEntry={renderEntry}
    />
  );
}

export default RegulationHistory;
