import React, { useState, useEffect } from 'react';
import {
  Box, IconCirclePlus, Timer,
  Table, TableHeader, TableRow, TableCell, Text, Help
} from '@aragon/ui';

import {Fieldset, Button, Divider, useTheme} from '@geist-ui/react'
import { SkipForward } from '@geist-ui/react-icons'
import {getEpoch, getEpochTime, getNextEpochTimestamp, getCurrentEpochLength
} from '../../utils/infura';
import {advance} from '../../utils/web3';
import {VTDD} from "../../constants/tokens";
// import AdvanceEpoch from './AdvanceEpoch';
// import EpochPageHeader from "./Header";
import IconHeader from "../common/IconHeader";

type ButtonTypes =
  | 'default'
  | 'secondary'
  | 'success'
  | 'warning'
  | 'error'
  | 'abort'
  | 'secondary-light'
  | 'success-light'
  | 'warning-light'
  | 'error-light'


function EpochDetail({ user }: {user: string}) {
  const [epoch, setEpoch] = useState(0);
  const [epochTime, setEpochTime] = useState(0);
  const [nextEpochTimestamp, setNextEpochTimestamp] = useState(0);
  const [currentEpochLength, setCurrentEpochLength] = useState(0);
  const [advanceMode, setAdvanceMode] = useState<ButtonTypes>('success');
  const theme = useTheme();
  var endDate = nextEpochTimestamp*1000
  var safetyEnd = (nextEpochTimestamp+91)

  function secondsToHms(d) {
    d = Number(d);
    var h = Math.floor(d / 3600);
    var m = Math.floor(d % 3600 / 60);
    var s = Math.floor(d % 3600 % 60);

    var hDisplay = h > 0 ? h + (h == 1 ? " hour, " : " hours, ") : "";
    var mDisplay = m > 0 ? m + (m == 1 ? " minute, " : " minutes, ") : "";
    var sDisplay = s > 0 ? s + (s == 1 ? " second" : " seconds") : "";
    return hDisplay + mDisplay + sDisplay; 
  }

  function calcAdvanceMode() {
    const now = Date.now()/1000 
    if (now < nextEpochTimestamp) {
      setAdvanceMode('error');
    } else {
      setAdvanceMode('success');
    }
  }

  var currentEpochLengthFmt = secondsToHms(currentEpochLength)

  useEffect(() => {
    let isCancelled = false;
    async function updateUserInfo() {
      const [epochStr, epochTimeStr, nextEpochTimestampStr, currentEpochLengthStr] = await Promise.all([
        getEpoch(VTDD.addr),
        getEpochTime(VTDD.addr),
        getNextEpochTimestamp(VTDD.addr),
        getCurrentEpochLength(VTDD.addr)

      ]);

      if (!isCancelled) {
        setEpoch(parseInt(epochStr, 10));
        setEpochTime(parseInt(epochTimeStr, 10));
        setNextEpochTimestamp(parseInt(nextEpochTimestampStr, 10));
        setCurrentEpochLength(parseInt(currentEpochLengthStr, 10));
        calcAdvanceMode();
      }
    }
    updateUserInfo();
    const id = setInterval(updateUserInfo, 1200);

    // eslint-disable-next-line consistent-return
    return () => {
      isCancelled = true;
      clearInterval(id);
    };
  }, [user]);

  return (
    <Fieldset style={{marginTop: '10px'}}>
      <Divider y={0} />
      <Fieldset.Content>
        <Fieldset.Subtitle>Current Epoch:  <span style={{color: theme.palette.accents_4}}>{epoch}</span></Fieldset.Subtitle>
        <Fieldset.Subtitle>Epoch Length: <span style={{color: theme.palette.accents_4}}>{currentEpochLengthFmt}</span></Fieldset.Subtitle>
        <Fieldset.Subtitle>Next Epoch In: </Fieldset.Subtitle>
        <Timer showEmpty={true} format='hms' end={endDate} /> 
      </Fieldset.Content>
      <Fieldset.Footer>
        <Fieldset.Footer.Status>
          <Text type="error">Advance the Epoch for reward:</Text>
        </Fieldset.Footer.Status>
        <Fieldset.Footer.Actions>
          <Button icon={<SkipForward />} auto size="mini" type={advanceMode}
               onClick={() => {
                advance(VTDD.addr);
              }}
          >Advance</Button>
        </Fieldset.Footer.Actions>
      </Fieldset.Footer>
    </Fieldset>
  );
}

export default EpochDetail;
