import React from 'react';
import {VTD, VTDD, PeggedTokens} from "../constants/tokens";
import BigNumber from 'bignumber.js';
import {
  getPoolTotalBonded,
  getTokenBalance,
  getOracleTwap,
  getInstantaneousPrice,
  getPoolTotalClaimable, getPoolTotalRewarded,
  getTokenTotalSupply, getTotalBonded, getTotalCoupons, getTotalDebt, getTotalRedeemable, getTotalStaged
} from '../utils/infura';
import { toTokenUnitsBN } from '../utils/number';

export interface ITodo {
  id: number
  title: string
  description: string
  status: boolean
}

export interface IPeggedToken {
  name: string,
  pair: {
    addr: string,
    decimals: number,
    symbol: string,
  },
  addr: string,
  decimals: number,
  symbol: string,
  pool: string,
  oracle: string,
  enabled: boolean,
  usdcPair: string,
  usdcPrice: BigNumber,
  stats: PegStats
}

export interface PegStats {
  vtdBalance: BigNumber,
  pegBalance: BigNumber,
  poolReward: BigNumber,
  poolClaimable: BigNumber,
  poolTotalBonded: BigNumber,
  price: BigNumber,
  lastTwap: BigNumber
}

export type ContextType = {
  peggedTokens: IPeggedToken[],
  totals: {
    totalSupply: BigNumber,
    totalBonded: BigNumber,
    totalStaged: BigNumber,
    totalLPSupply: BigNumber,
    totalLPBonded: BigNumber,
    circulating: BigNumber,
    totalRedeemable: BigNumber,
    totalDebt: BigNumber,
    totalCoupon: BigNumber
  },
  updatePools: () => void,
  updateTotals: () => {}
}

//@ts-ignore
const initialPeggedTokens: IPeggedToken[] = PeggedTokens.map((peg) => {
  peg.stats = {
    vtdBalance: new BigNumber(0),
    pegBalance: new BigNumber(0),
    poolReward: new BigNumber(0),
    poolClaimable: new BigNumber(0),
    poolTotalBonded: new BigNumber(0),
    price: new BigNumber(0),
    lastTwap: new BigNumber(0)
  } 

  peg.usdcPrice = new BigNumber(0)
  return peg
})

const initialTotals = {
  totalSupply: new BigNumber(0),
  totalBonded: new BigNumber(0),
  totalStaged: new BigNumber(0),
  totalLPSupply: new BigNumber(0),
  totalLPBonded: new BigNumber(0),
  circulating: new BigNumber(0),
  totalRedeemable: new BigNumber(0),
  totalDebt: new BigNumber(0),
  totalCoupon: new BigNumber(0)
}

export const MainContext = React.createContext<ContextType | null>(null);

export const MainStoreProvider: React.FC<React.ReactNode> = ({ children }) => {
  const [peggedTokens, setPeggedTokenPools] = React.useState<IPeggedToken[]>(initialPeggedTokens);
  const [totals, setTotals] = React.useState(initialTotals);

  const updatePools = () => {
    let pegs: IPeggedToken[] = []
    peggedTokens.forEach(async (peg) =>  { 
      if (peg.enabled) {
        let [vtdBalance, pegBalance, poolReward, poolClaimable, totalBonded, oracleTwap] = await Promise.all([
          getTokenBalance(VTD.addr, peg.pair.addr),
          getTokenBalance(peg.addr, peg.pair.addr),
          getPoolTotalRewarded(peg.pool),
          getPoolTotalClaimable(peg.pool),
          getPoolTotalBonded(peg.pool),
          getOracleTwap(peg.oracle)
          ]);
        
        let usdcPrice = new BigNumber(1e18);
        if (!(peg.symbol === 'USDC')) {
           let multiplier = peg.decimals == 18 ? 1e12 : 1;
           const [rawUsdcPrice] = await Promise.all([getInstantaneousPrice('0xA0b86991c6218b36c1d19D4a2e9Eb0cE3606eB48', peg.usdcPair)])
           usdcPrice = rawUsdcPrice.multipliedBy(new BigNumber(multiplier));
        }

        peg.stats = {
          vtdBalance: toTokenUnitsBN(vtdBalance, VTD.decimals),
          pegBalance: toTokenUnitsBN(pegBalance, peg.decimals),
          poolReward: toTokenUnitsBN(poolReward, VTD.decimals),
          poolClaimable: toTokenUnitsBN(poolClaimable, VTD.decimals),
          poolTotalBonded: toTokenUnitsBN(totalBonded, VTD.decimals),
          price: toTokenUnitsBN(pegBalance, peg.decimals).dividedBy(toTokenUnitsBN(vtdBalance, VTD.decimals)),
          lastTwap: toTokenUnitsBN(new BigNumber(oracleTwap), VTD.decimals),
        }

        peggedTokens.filter((_peg: IPeggedToken) => {
          if (_peg.symbol === peg.symbol) {
            _peg.stats = peg.stats;
            _peg.usdcPrice = usdcPrice;
            setPeggedTokenPools([...peggedTokens]);
          }
        });
      }
    })
  };

  const updateTotals = async () => {
    const [
      totalSupplyStr,
      totalBondedStr, totalStagedStr, totalRedeemableStr,
      totalDebtStr, totalCouponsStr
      ] = await Promise.all([
      getTokenTotalSupply(VTD.addr),

      getTotalBonded(VTDD.addr),
      getTotalStaged(VTDD.addr),
      getTotalRedeemable(VTDD.addr),

      getTotalDebt(VTDD.addr),
      getTotalCoupons(VTDD.addr)

      ]);

    let poolTotal = new BigNumber(0);
    let totalLiquidityPoolSupply = new BigNumber(0);
    peggedTokens.forEach((peg) => {
      totalLiquidityPoolSupply = totalLiquidityPoolSupply.plus(peg.stats.poolReward)
        .plus(peg.stats.poolClaimable)
        poolTotal = poolTotal.plus(peg.stats.vtdBalance)
    })

    const newTotals = {
      totalSupply: toTokenUnitsBN(totalSupplyStr, VTD.decimals),
      totalBonded: toTokenUnitsBN(totalBondedStr, VTD.decimals),
      totalStaged: toTokenUnitsBN(totalStagedStr, VTD.decimals),
      totalLPSupply: totalLiquidityPoolSupply,
      totalLPBonded: poolTotal,
      circulating: new BigNumber(0),
      totalRedeemable: toTokenUnitsBN(totalRedeemableStr, VTD.decimals),
      totalDebt: toTokenUnitsBN(totalDebtStr, VTD.decimals),
      totalCoupon: toTokenUnitsBN(totalCouponsStr, VTD.decimals)
    }
    newTotals.circulating = newTotals.totalSupply.minus(newTotals.totalLPBonded)
                                                 .minus(newTotals.totalBonded)
                                                 .minus(newTotals.totalStaged)
                                                 .minus(newTotals.totalLPSupply)
    setTotals(newTotals);
  }

  return (
    <MainContext.Provider value={{peggedTokens, totals, updatePools, updateTotals }}>
      {children}
    </MainContext.Provider>
  );
};

export default MainStoreProvider;
