import { Line } from 'react-chartjs-2';

import React, { useContext, useState, useEffect } from 'react';
import BigNumber from 'bignumber.js';

import {Card, Grid, Link, Spacer, useMediaQuery, useTheme, Button, Text, Image, Display, Input} from '@geist-ui/react'
import { StatsRowItem } from '../common/index';
import {formatBN} from "../../utils/number";
import {MOMENTUM_FACTOR} from "../../constants/values";
import {EpochContext, ContextType } from "../../store/EpochStore";
import {MainContext, ContextType as MainContextType } from "../../store/MainStore";

import { createVoidZero } from 'typescript';

function fm(bn){
  let num = (formatBN(bn, 2))
  return num;
}

const Playground = ({opened}: {opened: boolean}) => {
  const theme = useTheme();
  const { peggedTokens } = React.useContext(MainContext) as MainContextType;
  const { regulations, epochData, updateRegulations} = React.useContext(EpochContext) as ContextType;

  const downMD = useMediaQuery('md', { match: 'down' })
  const upSM = useMediaQuery('sm', { match: 'up' })

  const [addEpoch, setAddEpoch] = useState<string>('1.1')

  const [_prices, setPrices] = useState<number[]>([])
  const [_momentumPrice, setMomentumPrice] = useState<number[]>([])
  const [_labels, setLabels] = useState<number[]>([])

  const addToEpochs = function() {
    const newPrice = parseFloat(addEpoch);
    const newMomentum = MOMENTUM_FACTOR*latestMomentum + (1-MOMENTUM_FACTOR)*newPrice
    setPrices([..._prices, newPrice])
    setMomentumPrice([..._momentumPrice, newMomentum])
    setLabels([..._labels, _labels[_labels.length -1]+1])
  }

  const addRandomEpoch = function() {
    const newPrice = latestPrice + (Math.random() > 0.3 ? Math.random()*latestPrice : -1*Math.random()*latestPrice);
    const newMomentum = MOMENTUM_FACTOR*latestMomentum + (1-MOMENTUM_FACTOR)*newPrice
    setPrices([..._prices, newPrice])
    setMomentumPrice([..._momentumPrice, newMomentum])
    setLabels([..._labels, _labels[_labels.length -1]+1])
  }

  const startWithExistingPrices = function() {
    setPrices([...prices])
    setMomentumPrice([...momentumPrice])
    setLabels([...labels])
  }

  let price =  new BigNumber(0);
  let lastTwap = new BigNumber(0);
  peggedTokens.filter((_peg) => {
    if (_peg.symbol === 'DSD') {
      price = _peg.stats.price
      lastTwap = _peg.stats.lastTwap
    }
  })

  // chart data
  let regs = regulations.reverse().slice(-15)
  const prices = regs.map((reg) => Number(reg.price.toNumber().toFixed(2)));
  const momentumPrice = regs.map((reg) =>  reg.momentumPrice ? Number(reg.momentumPrice.toNumber().toFixed(2)): 0);
  const labels = regs.map((x) => x.epoch)

  // @ts-ignore

  const timeElapsed = Date.now()/1000 - epochData.previousEpochTimestamp;
  const priceWeight = (timeElapsed/epochData.currentEpochLength)*1.2;
  const estimatedTwap = price.isZero() ? 0 : priceWeight*price.toNumber() + (1-priceWeight)*lastTwap.toNumber();
  const latestMomentum = _momentumPrice[_momentumPrice.length -1] || 0
  const latestPrice = _prices[_prices.length -1] || 0
  const latestType = latestPrice > latestMomentum ? 'INCREASE' : 'DECREASE'


  const price_data = {
      labels: _labels.slice(-15),
      datasets: [
        {
          label: 'Price ($)',
          data: _prices.slice(-15),
          backgroundColor: 'rgba(75,192,192,0.4)',
          borderColor: 'rgba(75,192,192,1)',
          borderCapStyle: 'butt',
          borderDash: [],
          borderDashOffset: 0.0,
          borderJoinStyle: 'miter',
          pointBorderColor: 'rgba(75,192,192,1)',
          pointBackgroundColor: '#fff',
          pointBorderWidth: 1,
          pointHoverRadius: 5,
          pointHoverBackgroundColor: 'rgba(75,192,192,1)',
          pointHoverBorderColor: 'rgba(220,220,220,1)',
          pointHoverBorderWidth: 2,
          pointRadius: 3,
        },
        {
          label: 'Momentum ($)',
          borderDash: [5,15],
          backgroundColor: 'rgba(255,99,132,0.2)',
      borderColor: 'rgba(255,99,132,1)',
      borderWidth: 1,
      hoverBackgroundColor: 'rgba(255,99,132,0.4)',
      hoverBorderColor: 'rgba(255,99,132,1)',
          data: _momentumPrice.slice(-15)
        }
      ]
    };
  const options = {
    responsive: true,
    layout: {
        padding: {
            left: 10,
            right: 10,
            top: 10,
            bottom: 10
        }
    },
    scales: {
      yAxes: [{
        display: downMD ? false :true,
        position: 'right',
        ticks: {
          callback: function(label, index, labels) {
              return '$' + label;
          }
      },
      }],
      xAxes: [{
        display: downMD ? false :true,
        ticks: {
          callback: function(label, index, labels) {
              return 'Ep ' + label;
          }
        }
      }]
    }
  }

  useEffect(() => {
    startWithExistingPrices();
  },[opened])
  return (
      <>
        <Text h2>What is Dynamic Pegging?</Text>
        <Grid.Container gap={2} justify="center">
        <Grid xs={24} md={12}>
            <Text p>
            Unlike traditional stablecoins with a peg to a fiat currency (USDC, USDT, DAI, DSD, ESD) or some newer attempts to peg to another stablecoin (ZAI), VTDs dynamic peg is not pegged any currency, asset or form of collateral. It is a first attempt to adopt a floating peg that finds its intrinsic value based on supply and demand of the market.
            </Text>
            <Text p>
            Ultimately, and over cycles of expansion and contraction, market dynamics will determine a fair value for the peg that will adjust to new conditions in the overall cryptocurrency and Defi market. In our view, this is a self-fulfilling prophecy based on community trust and a certain saturation of VTDs market capitalization.
            </Text>
            <Text p>
            We expect a gradual convergence of VTDs intrinsic value to the overall cryptocurrency or Defi market capitalization. As the total crypto market cap grows, we expect the expansion and contraction cycles of VTD to oscillate and drift upwards as shown in the following graph. Of course, the opposite is true during crypto winters  but as we know crypto always comes back even stronger after periods of consolidation.
            </Text>
            <Link icon color href="https://vtdollar.medium.com/faq-dynamic-peg-mechanism-in-the-vtd-ecosystem-96396d648baf" target="_blank">Read more about it on Medium</Link>
        </Grid>
        <Grid xs={24} md={12}>
            <Display shadow caption={<p>Theoretical path of convergence</p>}>
            <Image src="peg-graph.jpeg" />
            </Display>
        </Grid> 
        </Grid.Container>
        <Text h2>Experiment with Dynamic Pegging</Text>
        <Text p>
            Play with possible epochs and see how they affect expansion/contraction:
        </Text>
        <Card type="lite" style={{fontSize: '12px', background: theme.palette.accents_1}}>
          <Button onClick={() => startWithExistingPrices()}>Start With Existing Prices</Button>
          <Spacer></Spacer>
          <Input value={addEpoch} onChange={(e) => setAddEpoch(e.target.value)} label="TWAP Price" placeholder="1.1" />
          <Button onClick={() => addToEpochs()}>Add Epoch</Button>
          <Button onClick={() => addRandomEpoch()}>Add Random Epoch</Button>

        </Card>
        <Card style={{display: 'flex'}}>
        <Card type="secondary" shadow style={{fontSize: '12px'}}>
            <Grid.Container gap={2} justify="center">

              <Grid xs={24} md={6}>
                Momentum Price: <span style={{color: theme.palette.accents_2, fontSize: '12px'}}>{latestMomentum.toFixed(2) || 0}DSD</span>
              </Grid>
              <Grid xs={24} md={6}>
              Last TWAP: <span style={{color: theme.palette.accents_2, fontSize: '12px'}}>{latestPrice.toFixed(2)}DSD</span>
              </Grid>
              <Grid xs={24} md={6}>
              Current Epoch: <span style={{color: theme.palette.accents_2, fontSize: '12px'}}>{latestType === 'INCREASE' ? 'Expansion' : 'Contraction'}</span>
              </Grid>
              </Grid.Container>
          </Card>
            
            <Line 
            height={upSM ? 55 : 400}
                            legend={{display: false}}
                            data={price_data}
                            options={options}/> 

            
        </Card>
    </>
  )
};


export default Playground;
