import { Line } from 'react-chartjs-2';

import React, { useContext, useState, useEffect } from 'react';
import BigNumber from 'bignumber.js';

import {Card, Grid, Page, useMediaQuery, useTheme, Loading, Button} from '@geist-ui/react'
import { StatsRowItem } from '../common/index';
import {formatBN} from "../../utils/number";
import {MOMENTUM_FACTOR} from "../../constants/values";
import {EpochContext, ContextType } from "../../store/EpochStore";
import {MainContext, ContextType as MainContextType } from "../../store/MainStore";
import Playground from "./Playground"
import Overlay from '../layout/overlay'
import { createVoidZero } from 'typescript';

function fm(bn){
  let num = (formatBN(bn, 2))
  return num;
}

const MomentumHeader = ({ user }: {user: string}) => {
  const theme = useTheme();
  const [loaded, setLoaded] = useState(false);
  const [playgroundOpened, setPlaygroundOpened] = useState(false)

  const { peggedTokens } = React.useContext(MainContext) as MainContextType;
  const { regulations, epochData, updateEpochData, updateRegulations} = React.useContext(EpochContext) as ContextType;

  const downMD = useMediaQuery('md', { match: 'down' })
  const upSM = useMediaQuery('sm', { match: 'up' })

  let price =  new BigNumber(0);
  let lastTwap = new BigNumber(0);
  peggedTokens.filter((_peg) => {
    if (_peg.symbol === 'DSD') {
      price = _peg.stats.price
      lastTwap = _peg.stats.lastTwap
    }
  })

  // chart data
  let regs = regulations.reverse().slice(-15)
  const prices = regs.map((reg) => Number(reg.price.toNumber().toFixed(2)));
  const momentumPrice = regs.map((reg) =>  reg.momentumPrice ? Number(reg.momentumPrice.toNumber().toFixed(2)): 0);
  const labels = regs.map((x) => x.epoch)
  // @ts-ignore
  const latestEpoch = regs[regs.length - 1];

  const timeElapsed = Date.now()/1000 - epochData.previousEpochTimestamp;
  const priceWeight = (timeElapsed/epochData.currentEpochLength)*1.2;
  const estimatedTwap = price.isZero() ? 0 : priceWeight*price.toNumber() + (1-priceWeight)*lastTwap.toNumber();
  const latestMomentum = latestEpoch ? latestEpoch.momentumPrice : new BigNumber(0)
  // @ts-ignore
  const estimatedMomentum = estimatedTwap == 0 ? 0 : MOMENTUM_FACTOR*estimatedTwap + (1-MOMENTUM_FACTOR)*latestMomentum.toNumber()
  const nextChange = estimatedMomentum > estimatedTwap ? 'Contraction' : 'Expansion'  
  const delta = Math.abs(estimatedMomentum - estimatedTwap).toFixed(2)

  const price_data = {
      labels: labels,
      datasets: [
        {
          label: 'Price ($)',
          data: prices,
          backgroundColor: 'rgba(75,192,192,0.4)',
          borderColor: 'rgba(75,192,192,1)',
          borderCapStyle: 'butt',
          borderDash: [],
          borderDashOffset: 0.0,
          borderJoinStyle: 'miter',
          pointBorderColor: 'rgba(75,192,192,1)',
          pointBackgroundColor: '#fff',
          pointBorderWidth: 1,
          pointHoverRadius: 5,
          pointHoverBackgroundColor: 'rgba(75,192,192,1)',
          pointHoverBorderColor: 'rgba(220,220,220,1)',
          pointHoverBorderWidth: 2,
          pointRadius: 3,
        },
        {
          label: 'Momentum ($)',
          borderDash: [5,15],
          backgroundColor: 'rgba(255,99,132,0.2)',
      borderColor: 'rgba(255,99,132,1)',
      borderWidth: 1,
      hoverBackgroundColor: 'rgba(255,99,132,0.4)',
      hoverBorderColor: 'rgba(255,99,132,1)',
          data: momentumPrice
        }
      ]
    };
  const options = {
    responsive: true,
    layout: {
        padding: {
            left: 10,
            right: 10,
            top: 10,
            bottom: 10
        }
    },
    scales: {
      yAxes: [{
        display: downMD ? false :true,
        position: 'right',
        ticks: {
          callback: function(label, index, labels) {
              return '$' + label;
          }
      },
      }],
      xAxes: [{
        display: downMD ? false :true,
        ticks: {
          callback: function(label, index, labels) {
              return 'Ep ' + label;
          }
        }
      }]
    }
  }

  useEffect(() => {
      updateRegulations();
      updateEpochData();
      setLoaded(true)
  },[user])
  return (
      <Card style={{display: 'flex'}}>
        {(regs.length < 1) ? 
        <Loading /> :
        <>
          <Card type="secondary" shadow style={{fontSize: '12px'}}>
            <Grid.Container gap={2} justify="center">

              <Grid xs={24} md={6}>
                Momentum Price: <span style={{color: theme.palette.accents_2, fontSize: '12px'}}>{latestEpoch.momentumPrice?.toFixed(2) || 0}DSD</span>
              </Grid>
              <Grid xs={24} md={6}>
              Last TWAP: <span style={{color: theme.palette.accents_2, fontSize: '12px'}}>{latestEpoch.price.toFixed(2)}DSD</span>
              </Grid>
              <Grid xs={24} md={6}>
              Current Epoch: <span style={{color: theme.palette.accents_2, fontSize: '12px'}}>{latestEpoch.type === 'INCREASE' ? 'Expansion' : 'Contraction'}</span>
              </Grid>
              <Grid xs={24} md={6}>
              <Button size="small" onClick={() => setPlaygroundOpened(true)}>Learn Dynamic Pegging</Button>
              </Grid>
              </Grid.Container>
          </Card>
          <Card type="lite" shadow style={{fontSize: '12px', background: theme.palette.accents_3}}>
            <Grid.Container gap={2} justify="center">

                <Grid xs={24} md={6}>
                Estimated Momentum Price: <span style={{color: theme.palette.accents_6, fontSize: '12px'}}>{estimatedMomentum.toFixed(2) || 0}DSD</span>
                </Grid>
                <Grid xs={24} md={6}>
                Estimated TWAP: <span style={{color: theme.palette.accents_6, fontSize: '12px'}}>{estimatedTwap.toFixed(2)}DSD</span>
                </Grid>
                <Grid xs={24} md={6}>
                Predicted Next Epoch: <span style={{color: theme.palette.accents_6, fontSize: '12px'}}>{nextChange}</span>
                </Grid>
                {nextChange == 'Contraction' ?
                  <Grid xs={24} md={6}>
                  Distance from Expansion: <span style={{color: theme.palette.accents_6, fontSize: '12px'}}>{delta}DSD</span>
                  </Grid> :
                  <Grid xs={24} md={6}>
                  Distance from Contraction: <span style={{color: theme.palette.accents_6, fontSize: '12px'}}>{delta}DSD</span>
                  </Grid> 
                }
  
                </Grid.Container>
            </Card>
        </>
      }
        
          <Line height={upSM ? 55 : 400}
                          legend={{display: false}}
                          data={price_data}
                          options={options}/> 

      <Overlay
        visible={playgroundOpened}
        onClose={() => setPlaygroundOpened(false)}
      >
      <Page>
        <Playground opened={playgroundOpened}/>
      </Page>
      </Overlay>
      </Card>
  )
};


export default MomentumHeader;
