export interface Props {
  hasWeb3: boolean,
  user: string,
  setUser: Function,
  closeSideBar: Function,
  switchTheme: Function
}

export type SideChildren = Sides | Array<Sides>

export const SideGroup: React.FC<{ sides?: SideChildren, closeSideBar }> = React.memo(({ sides, closeSideBar }) => {
  if (!sides) return null
  sides = Array.isArray(sides) ? sides : [sides]
  return (
    <SideItem sides={sides} closeSideBar={closeSideBar}>
      <SideGroup closeSideBar={closeSideBar}/>
    </SideItem>
  )
})

